@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap";
#cal_holder {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

#cal {
  background-color: var(--secondary-color);
  color: var(--text-color);
  border: 3px solid gray;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 19dvw;
  height: fit-content;
  min-height: 40dvh;
  padding: 10px 0 0;
  display: flex;
  position: relative;
}

input {
  text-align: center;
  color: #000;
  letter-spacing: 1px;
  text-align: right;
  background-color: #0000;
  border: none;
  border-radius: 10px;
  outline: none;
  width: 17vw;
  height: 5dvh;
  padding: 10px 5px 5px;
  font-weight: 600;
}

#input_holder, #output_holder {
  box-sizing: border-box;
  justify-content: end;
  align-self: flex-end;
  width: 100%;
  padding: 0 10px;
  display: flex;
}

#input::placeholder {
  color: var(--text-color);
}

#output::placeholder {
  color: orange;
}

#input {
  color: var(--text-color);
  padding: 3px;
  font-size: 3dvh;
}

#output {
  color: orange;
  font-size: 3dvh;
}

input::selection {
  color: #000;
  background-color: #fff;
}

#key_holder, #key_holder_adv {
  box-sizing: border-box;
  border-radius: 10px;
  border-radius: inherit;
  background-color: #272727;
  gap: 10px;
  min-width: 19dvw;
  min-height: 24vh;
  margin: 10px 0 0;
  padding: 10px;
  display: flex;
}

#key_holder_adv {
  height: fit-content;
  display: none;
}

.cal_btn, .cal_btn_adv {
  color: var(--text-color);
  cursor: pointer;
  background-color: #1e201a;
  border: none;
  border-radius: 100px;
  width: fit-content;
  margin: 2px;
  padding: 1dvw;
  font-family: Roboto, "sans-serif";
  font-size: 2dvh;
  font-weight: 600;
}

button:hover {
  background-color: #000;
}

#num_holder {
  grid-template-columns: repeat(3, 1fr);
  width: 60%;
  display: grid;
}

#num_holder_adv {
  flex-wrap: wrap;
  width: 100%;
  height: fit-content;
  display: flex;
}

#operation_holder {
  grid-template-columns: repeat(2, 1fr);
  width: 40%;
  display: grid;
}

.operation-btn {
  color: orange;
}

#history_btn {
  color: #000;
  cursor: pointer;
  width: 10dvh;
  font-family: Roboto;
  font-size: 5dvh;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-90%, -10%);
}

#adv_btn {
  color: #000;
  cursor: pointer;
  width: 7dvh;
  transform: translate(-120%);
  transform: rotateY("30deg");
  transform-origin: 100% 0;
  font-family: Roboto;
  position: absolute;
  bottom: 0;
  left: 0;
}

#history_holder {
  z-index: 10;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: none;
  position: absolute;
}

#hist_title {
  margin-bottom: 2rem;
  font-size: 3dvh;
}

#history {
  color: #000;
  box-sizing: border-box;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 70dvw;
  height: 70dvh;
  padding: 2%;
  font-family: sans-serif;
  display: flex;
  position: relative;
}

#hist_close {
  cursor: pointer;
  background-color: #0000;
  border: none;
  font-size: 4dvh;
  position: absolute;
  top: 4%;
  right: 2%;
}

#history ul {
  flex-direction: column;
  gap: 3dvh;
  width: 100%;
  height: 70%;
  margin-top: 10px;
  display: flex;
  overflow: scroll;
}

#history li {
  text-align: center;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  list-style-type: none;
  display: flex;
}

#history-list li span {
  color: #fff;
  white-space: nowrap;
  background-color: #000;
  border-radius: .5rem;
  height: 3dvh;
  padding: .5rem;
  font-size: 2dvh;
  overflow: scroll;
}

.expression {
  text-align: start;
  background-color: #fff;
  flex: 0 0 60%;
  overflow: scroll;
}

.result {
  text-align: right;
  text-align: start;
  flex: 0 0 20%;
  overflow: scroll;
}

@media screen and (width <= 600px) {
  #cal {
    width: 65vw;
    height: fit-content;
  }

  input {
    width: 70vw;
  }

  #key_holder, #key_holder_adv {
    min-width: 65vw;
    height: fit-content;
  }

  .key_holder2 {
    transition: all 2s ease-in-out;
  }

  #cal.advance {
    width: 90vw;
  }

  #history_btn {
    display: none;
  }

  #adv_btn {
    width: 5dvh;
    font-size: 4dvh;
    transform: translate(-100%);
  }

  .cal_btn, .cal_btn_adv {
    padding: 3dvw;
  }

  #history {
    width: 90dvw;
    height: 70dvh;
  }

  .expression {
    text-align: start;
    background-color: #fff;
    flex: 0 0 40%;
    overflow: scroll;
  }

  .result {
    text-align: right;
    text-align: start;
    flex: 0 0 30%;
    overflow: scroll;
  }
}
